.App-header {
  /* background-color: var(--Black); */

  height: var(--Header-Default);
  padding: 1rem;
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: nowrap;
  background-color: var(--Grey100);
  align-items: center;
}

.App-search {
  background-color: white;
  height: -webkit-min-content;
  height: min-content;
  border-radius: 5px;
}
.App-search input {
  border: none;
  width: 100%;
  border-radius: 5px;
}
.SearchField-Container {
}
#ParameterSearch {
  width: 13vw;
}
#DescriptionSearch {
  width: 23vw;
}
#UnitSearch {
  width: 8vw;
}
#RigFamSearch {
  width: 8vw;
}
#SensorSearch {
  width: 13vw;
}
#CommentSearch {
  width: 23vw;
}

/* Responsive */
@media screen and (max-width: 1250px) {
  .App-header {
    flex-wrap: wrap;
  }
  #ParameterSearch {
    width: 26vw;
  }
  #DescriptionSearch {
    width: 46vw;
  }
  #UnitSearch {
    width: 13vw;
  }
  #RigFamSearch {
    width: 13vw;
  }
  #SensorSearch {
    width: 26vw;
  }
  #CommentSearch {
    width: 46vw;
  }
}
@media screen and (max-width: 750px) {
  .App-header {
    flex-wrap: wrap;
    height: var(--Header-750);
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
  }

  #ParameterSearch {
    width: 60vw;
    order: 0;
  }
  #DescriptionSearch {
    flex: 90vw 1;
    order: 2;
  }
  #UnitSearch {
    width: 30vw;
    order: 1;
  }
  #RigFamSearch {
    width: 30vw;
    order: 3;
  }
  #SensorSearch {
    width: 60vw;
    order: 4;
  }
  #CommentSearch {
    flex: 90vw 1;
    order: 5;
  }
}
@media screen and (max-width: 320px) {
  .App-header {
    flex-wrap: wrap;
    height: var(--Header-320);
    flex-flow: row wrap;
  }
  .SearchField-Container {
    margin: 0.2rem 0;
  }

  #ParameterSearch {
    width: 100vw;
  }
  #DescriptionSearch {
    width: 100vw;
  }
  #UnitSearch {
    width: 100vw;
  }
  #RigFamSearch {
    width: 100vw;
  }
  #SensorSearch {
    width: 100vw;
  }
  #CommentSearch {
    width: 100vw;
  }
}

.SearchField-Label {
  color: var(--Grey600);
  transition: top 0.1s ease-in-out, left 0.1s ease-in-out,
    opacity 0.1s ease-in-out, z-index 0.1s ease-out,
    color 0.1s ease-in-out, background-color 0.1s ease-in-out, -webkit-transform 0.1s ease-in-out;
  transition: top 0.1s ease-in-out, left 0.1s ease-in-out,
    transform 0.1s ease-in-out, opacity 0.1s ease-in-out, z-index 0.1s ease-out,
    color 0.1s ease-in-out, background-color 0.1s ease-in-out;
  transition: top 0.1s ease-in-out, left 0.1s ease-in-out,
    transform 0.1s ease-in-out, opacity 0.1s ease-in-out, z-index 0.1s ease-out,
    color 0.1s ease-in-out, background-color 0.1s ease-in-out, -webkit-transform 0.1s ease-in-out;
  background-color: none;
  position: absolute;
  opacity: 0;
  top: 50%;
  left: 0.5rem;
  z-index: -1;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
}
.show-label {
  opacity: 1;
  top: -0.7em;
  left: 5px;
  z-index: 1;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  transition: top 0.1s ease-in-out, left 0.1s ease-in-out,
    opacity 0s ease-in-out, z-index 0s ease-in-out,
    color 0.2s ease-in-out, background-color 0.1s ease-in-out, -webkit-transform 0.1s ease-in-out;
  transition: top 0.1s ease-in-out, left 0.1s ease-in-out,
    transform 0.1s ease-in-out, opacity 0s ease-in-out, z-index 0s ease-in-out,
    color 0.2s ease-in-out, background-color 0.1s ease-in-out;
  transition: top 0.1s ease-in-out, left 0.1s ease-in-out,
    transform 0.1s ease-in-out, opacity 0s ease-in-out, z-index 0s ease-in-out,
    color 0.2s ease-in-out, background-color 0.1s ease-in-out, -webkit-transform 0.1s ease-in-out;
  color: var(--Highlight-Active);
  background-color: var(--Grey100);
}
.SearchField-Container {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}
.Search-Container {
  border: none;
  border-radius: 5px;
  padding: 0;
  height: 5vh;
  max-height: 40px;
  position: relative;
  background-color: var(--Grey100);
  width: 100%;
  margin: 0;
}
.SearchField-Input:focus .Search-Container {
  border: #004fbc 1px solid;
}

.Search-Icon {
  position: absolute;
  top: 50%;
  right: 0.5rem;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  color: var(--Grey600);
  cursor: pointer;
}

.SearchField-Input {
  width: 100%;
  height: 100%;
  line-height: 6ex;
  border-radius: 5px;
  border: var(--Grey400) 2px solid;
  background-color: var(--Grey100);
  transition: all 0.2s ease-in-out;
  height: 35px;
  padding: 0 2.3rem 0 0.5rem;
  
}

.SearchField-Input:focus {
  outline: none;
  border: var(--Highlight-Active) 2px solid;
  transition: all 0.2s ease-in-out;
}

.form-group > input {
  padding: 0 0 0 10px;
}
.Dropdown-Container-Div * {
  z-index: 4;
}
.Dropdown-Container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  margin-top: 5px;
  border: var(--Grey300) 2px solid;
  max-height: 30vh;
  overflow-y: scroll;
  overflow-x: auto;
  padding: 0;
}
.Dropdown-Container li {
  color: black;
  font-size: 1rem;
  font-weight: 300;
  cursor: pointer;
  border-bottom: var(--Grey300) 1px solid;
  width: 100%;
  list-style: none;
  padding: 0.25rem 0.5rem;
  background-color: var(--Grey100);
  z-index: 4;
}
.Dropdown-Container li.selected  {
  background-color: var(--Grey300)
}

:root {
  --Row-Height: 35px;
}

.Table-Container {
  overflow-y: auto;
  width: 100vw;
}

table {
  width: 100vw;
  text-align: center;
  overflow-x: scroll;
  table-layout: fixed;
  border-collapse: collapse;  
}
thead {
  background-color: var(--Black);
  color: var(--White);

}
thead.sticky {
  position: fixed;
  top: 0;
  z-index: 1;
  width: 100vw;
}
thead th {
  padding: 0.5rem;
  
}
tbody td {
  border-bottom: 1px solid var(--Grey300);
  overflow: hidden;
}
td, th {
  height: 35px;
  height: var(--Row-Height);
  text-overflow: ellipsis;
  white-space: nowrap;
}
tbody tr {
  overflow: hidden;
}

#TableName {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
#TableDescription {
  overflow: hidden;
}
.Table-Arrow {
  transition: -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  padding: 0;
}
.Table-Arrow:hover {
  cursor: pointer;
}
.Table-Arrow svg {
  height: 30px;
  width: 30px;
  fill: var(--Grey600);
}
tr.Active-Row .Table-Arrow {
  -webkit-transform: scale(1, -1);
          transform: scale(1, -1);
  transition: -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}
.Expandable-Area {
  background-color: #f5f5f5;
  height: calc(35px * 6);
  height: calc(var(--Row-Height) * 6);
}
.Expandable-Row {
  display: none;
}
.Expandable-Row.Active-Row {
  display: table-row;
}
.Images-Container {
  /* Buttons over image */
  /* TODO Add counter and hide arrow when movement stopped*/
  position: relative;
  height: 80%;
  width: 20vw;
  border: 1px var(--Grey300) solid;
  overflow: hidden;
}
.Images-Container img {
  height: 100%;
  width: 100%;
  object-fit: contain;
  display: block;
  
}
.Image.Active {
  display: block;
}
.Image-Nav{
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: 1;
  display: flex;
  justify-content: center;
  width: 100%;
}
.Image-Nav svg {
  stroke: var(--Grey300);
  stroke-width: 0.5px;
}
.Image-Nav svg:hover {
  cursor: pointer;
  stroke: var(--Grey600);
}
.Image-Nav svg.Disabled {
  opacity: 0;
}


body {
  margin: 0;
  padding: 0;
}
* {
  box-sizing: border-box;
}

:root {
  --Dark_Blue: #202a44;
  --White: #ffffff;
  --Black: #141414;

  --Grey100: #f7f7f7;
  --Grey200: #e1dfdd;
  --Grey300: #d0d0d0;
  --Grey400: #a7a8a9;
  --Grey500: #888b8d;
  --Grey600: #53565a;
  --Grey700: #323232;

  --Highlight-Default: #004fbc;
  --Highlight-Hover: #2b8ede;
  --Highlight-Active: #104e9b;

  --Success-Default: #1a6c31;

  --Warning-Default: #ffa000;

  --Error-Default: #bf2012;

  --Context-Default: #346559;

  --Header-Default: 150px;
  --Header-750: 240px;
  --Header-320: 320px;
}

